<template>
  <div class="login-container">
    <div class="login-box">
      <label for="username">Usuario:</label>
      <input type="text" id="username" v-model="username" />
      <label for="password">Contraseña:</label>
      <input type="password" id="password" v-model="password" />
      <button @click="handleLogin">Iniciar sesión</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: '',
      password: '',
    };
  },
  methods: {
    handleLogin() {
      if (this.username === 'carlos@example.com' && this.password === '123456') {
        this.$router.push('/menu'); // Redirige al menú si las credenciales son correctas
      } else {
        alert('Usuario o contraseña incorrectos');
      }
    },
  },
};
</script>

<style scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #b3d9ff; /* Azul claro */
}
.login-box {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}
</style>
