<template>
    <div class="empleados-container">
      <h1>Gestión de Empleados</h1>
      <div class="buttons-container">
        <button @click="currentAction = 'agregar'">Agregar Empleado</button>
        <button @click="currentAction = 'modificar'">Modificar Empleado</button>
        <button @click="currentAction = 'eliminar'">Eliminar Empleado</button>
      </div>
  
      <!-- Agregar Empleado -->
      <div v-if="currentAction === 'agregar'" class="form-container">
        <h3>Agregar Empleado</h3>
        <form @submit.prevent="addEmpleado">
          <label for="nombre">Nombre:</label>
          <input id="nombre" v-model="empleado.nombre" type="text" required />
  
          <label for="rol">Rol:</label>
          <input id="rol" v-model="empleado.rol" type="text" required />
  
          <label for="telefono">Teléfono:</label>
          <input id="telefono" v-model="empleado.telefono" type="text" required />
  
          <label for="correo">Correo:</label>
          <input id="correo" v-model="empleado.correo" type="email" required />
  
          <button type="submit">Guardar</button>
        </form>
      </div>
  
      <!-- Modificar Empleado -->
      <div v-if="currentAction === 'modificar'" class="form-container">
        <h3>Modificar Empleado</h3>
        <form @submit.prevent="updateEmpleado">
          <label for="empleadoSelect">Seleccionar Empleado:</label>
          <select v-model="selectedEmpleadoId" id="empleadoSelect" required>
            <option v-for="empleado in empleados" :key="empleado.id" :value="empleado.id">
              {{ empleado.nombre }}
            </option>
          </select>
  
          <label for="nombre">Nombre:</label>
          <input id="nombre" v-model="empleado.nombre" type="text" required />
  
          <label for="rol">Rol:</label>
          <input id="rol" v-model="empleado.rol" type="text" required />
  
          <label for="telefono">Teléfono:</label>
          <input id="telefono" v-model="empleado.telefono" type="text" required />
  
          <label for="correo">Correo:</label>
          <input id="correo" v-model="empleado.correo" type="email" required />
  
          <button type="submit">Modificar</button>
        </form>
      </div>
  
      <!-- Eliminar Empleado -->
      <div v-if="currentAction === 'eliminar'" class="form-container">
        <h3>Eliminar Empleado</h3>
        <label for="empleadoNombre">Nombre del Empleado:</label>
        <input id="empleadoNombre" v-model="empleadoNombre" type="text" required />
        <button @click="deleteEmpleado">Eliminar</button>
      </div>
  
      <!-- Mostrar tabla de empleados -->
      <div v-if="empleados.length > 0" class="tabla-container">
        <h3>Lista de Empleados</h3>
        <table>
          <thead>
            <tr>
              <th>Nombre</th>
              <th>Rol</th>
              <th>Teléfono</th>
              <th>Correo</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="empleado in empleados" :key="empleado.id">
              <td>{{ empleado.nombre }}</td>
              <td>{{ empleado.rol }}</td>
              <td>{{ empleado.telefono }}</td>
              <td>{{ empleado.correo }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </template>
  
  <script>
  import { collection, addDoc, getDocs, doc, updateDoc, deleteDoc, query, where } from "firebase/firestore";
  import { db } from "../firebase/firebase"; // Asegúrate de que db está exportado correctamente desde firebase.js
  
  export default {
    data() {
      return {
        empleados: [], // Lista de empleados
        empleado: {
          nombre: "",
          rol: "",
          telefono: "",
          correo: "",
        },
        empleadoNombre: "", // Para eliminar empleado por nombre
        selectedEmpleadoId: "", // ID del empleado a modificar
        currentAction: null, // Acción actual: agregar, modificar, eliminar
      };
    },
    methods: {
      // Método para obtener los empleados de Firestore
      async fetchEmpleados() {
        try {
          const querySnapshot = await getDocs(collection(db, "empleados"));
          this.empleados = querySnapshot.docs.map((doc) => ({
            id: doc.id,
            ...doc.data(),
          }));
        } catch (error) {
          console.error("Error al obtener los empleados:", error);
          alert("Hubo un error al cargar los empleados.");
        }
      },
  
      // Método para agregar un empleado a Firestore
      async addEmpleado() {
        try {
          if (!this.empleado.nombre || !this.empleado.rol || !this.empleado.telefono || !this.empleado.correo) {
            alert("Por favor, complete todos los campos.");
            return;
          }
  
          await addDoc(collection(db, "empleados"), {
            nombre: this.empleado.nombre,
            rol: this.empleado.rol,
            telefono: this.empleado.telefono,
            correo: this.empleado.correo,
          });
  
          alert("Empleado agregado exitosamente.");
          this.empleado = { nombre: "", rol: "", telefono: "", correo: "" };
          this.fetchEmpleados();
        } catch (error) {
          console.error("Error al agregar empleado:", error);
          alert("Hubo un error al agregar el empleado.");
        }
      },
  
      // Método para modificar un empleado
      async updateEmpleado() {
        try {
          if (!this.empleado.nombre || !this.empleado.rol || !this.empleado.telefono || !this.empleado.correo) {
            alert("Por favor, complete todos los campos.");
            return;
          }
  
          const empleadoRef = doc(db, "empleados", this.selectedEmpleadoId);
          await updateDoc(empleadoRef, {
            nombre: this.empleado.nombre,
            rol: this.empleado.rol,
            telefono: this.empleado.telefono,
            correo: this.empleado.correo,
          });
  
          alert("Empleado modificado exitosamente.");
          this.fetchEmpleados();
        } catch (error) {
          console.error("Error al modificar empleado:", error);
          alert("Hubo un error al modificar el empleado.");
        }
      },
  
      // Método para eliminar un empleado por nombre
      async deleteEmpleado() {
        try {
          if (!this.empleadoNombre) {
            alert("Por favor, ingresa el nombre del empleado a eliminar.");
            return;
          }
  
          // Buscar empleado por nombre
          const q = query(collection(db, "empleados"), where("nombre", "==", this.empleadoNombre));
          const querySnapshot = await getDocs(q);
          if (!querySnapshot.empty) {
            // Eliminar el primer empleado que coincida
            const docRef = querySnapshot.docs[0].ref;
            await deleteDoc(docRef);
            alert("Empleado eliminado exitosamente.");
            this.fetchEmpleados();
          } else {
            alert("No se encontró ningún empleado con ese nombre.");
          }
        } catch (error) {
          console.error("Error al eliminar empleado:", error);
          alert("Hubo un error al eliminar el empleado.");
        }
      },
    },
    mounted() {
      this.fetchEmpleados(); // Cargar empleados al iniciar el componente
    },
  };
  </script>
  
  <style scoped>
  /* Estilos para la interfaz */
  .empleados-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #e8f5e9;
  }
  
  .buttons-container {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  form {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  .tabla-container {
    margin-top: 20px;
  }
  
  table {
    border-collapse: collapse;
    width: 80%;
  }
  
  table,
  th,
  td {
    border: 1px solid #ddd;
  }
  
  th,
  td {
    padding: 10px;
    text-align: center;
  }
  </style>
  