<template>
    <div class="catalogo-container">
      <h1>Catálogo</h1>
      <div class="buttons-container">
        <button @click="goToFormulario">Formulario</button>
        <button @click="goToLaboratorio">Laboratorio</button>
        <button @click="goToServicios">Servicios</button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: "CatalogoView",
    methods: {
      goToFormulario() {
        // Navegar a la vista de formulario (puedes agregar una ruta específica)
        this.$router.push({ name: 'Formulario' });
      },
      goToLaboratorio() {
        // Navegar a la vista de laboratorio
        this.$router.push({ name: 'Laboratorio' });
      },
      goToServicios() {
        // Navegar a la vista de servicios
        this.$router.push({ name: 'servicios' });
      },
    },
  };
  </script>
  
  <style scoped>
  .catalogo-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f0f8ff;
  }
  
  .buttons-container {
    display: flex;
    gap: 20px;
    margin-top: 20px;
  }
  
  button {
    padding: 15px 30px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  </style>
  